import Nav_menu from '@/components/nav_menu/index.js';
import NavHeader from '@/components/nav_header/index.js';
import { getCurrentInstance } from "vue"; // import {localCache} from '@/util/cache.js'

export default {
  data() {
    return {
      width: "270px",
      asidestyle: ""
    };
  },

  components: {
    Nav_menu,
    NavHeader
  },
  methods: {
    init() {}

  },

  setup() {
    const datab = getCurrentInstance();

    const success = data => {
      console.log(data);
      datab.data.asidestyle = "transition:all .6s ease-out;width:" + datab.data.width + "px";
      datab.data.width = data;
    };

    return {
      success
    };
  }

};